@import "variables";
@import "mixins";

.view-taxonomy-term {
  margin: $margin 0;

  .view-content {
    display: flex;

    .views-row {
      flex: 0 1 40% !important;
    }
  }
}

@media (max-width: 900px) {
  .view-taxonomy-term {
    margin-top: $margin;

    .view-content {
      display: flex;

      .views-row {
        flex: 0 1 80% !important;
      }
    }
  }
}
